/* eslint-disable no-useless-concat */
import React, { useState, useEffect } from 'react';
import { Checkbox, Grid, Typography, Button, Alert } from '@mui/material';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import { useLocation } from 'react-router-dom';
import PlotlyPlotProny from './PronyPlot';
import { useNavigate } from 'react-router-dom';
import dataToServerProny from '../utils/dataToServerProny'
import AlertDialog from './ErrorAlert'
import { styles } from '../styles';
import GLOBAL from '../GLOBAL';

const serverAddress = GLOBAL.serverAddress;
function PronyAnalysis() {
    // ------------ Getting csv Data -----------------//
    const navigate = useNavigate();
    const location = useLocation();
    const [subLnData, data, time] = location.state || [null, null, null];
    // -------------- Making state variables ------------//
    const [selectedSub, setSelectedSub] = useState(null);
    const [selectedLine, setSelectedLine] = useState(null);
    const [property, setProperty] = React.useState('F');
    const [mode, setMode] = React.useState(null);
    const [buttonText, setButtonText] = useState("Detect event")
    const [plotData, setPlotData] = useState([[], []]);
    const [selectedPoints, setSelectedPoints] = useState([null, null]);
    const [err_message, setErr_message] = React.useState(null);
    const [pronyData, setPronyData] = useState([[], [],[]])

    let p1 = ['F', 'VM', 'VA', 'IM', 'IA']
    let p2 = ['Frequency', 'Voltage Magnitude', 'Voltage Angle', 'Current Magnitude', 'Current Angle']

    const tableStyle = {
        fontFamily: 'Arial, sans-serif',
        borderCollapse: 'collapse',
        width: '100%',
        
        
    };

    const thTdStyle = {
        border: '1px solid #dddddd',
        textAlign: 'left',
        padding: '8px',
    };

    const thStyle = {
        ...thTdStyle,
        backgroundColor: '#f2f2f2',
    };
    const tableRows = pronyData[0].map((frequencyComponent, index) => (
        <tr key={index}>
            <td>{frequencyComponent}</td>
            <td>{pronyData[1][index]}</td>
            <td>{pronyData[2][index]}</td>
        </tr>
    ));
    /* Concept of modes:
        null => default
        0 => after clicking on detect fault
        1 => after clicking on add 
        2 => after getting data from server for detect fault or getting fault data

        if mode is  0 or 1 => buttontext is detect fault
        if any of the sub, line or prop id changed mode is changed to 0


    */


    // --------------- handeling Button functions ------------------ // 

    const handleDetectFaultButton = () => {
        // console.log('Mode set to detect-event')
        setButtonText('Send To Server');
        setMode(0);

    }
    const changeDataSetToPlot = () => {
        if (selectedSub && selectedLine && property) {
            console.log(data[`${selectedSub}` + ':' + `${selectedLine}`][property])
            setPlotData(data[`${selectedSub}` + ':' + `${selectedLine}`][property]);
            setMode(1);
        }
        else {
            return;
        }
    }
    const resetPlotData = () => {
        setPlotData([[], []]);
        setMode(null);
    }
    const sendToServer = async (e) => {
        const serverData = await dataToServerProny([time, plotData, selectedPoints[0]], serverAddress + 'v3/prony-analysis');
        console.log('serverData', serverData);
        if (serverData.error) {
            if (serverData.error.message) {
                setErr_message("Server error while detecting");
            }
            else {
                setErr_message("Server error while detecting");
            }

            return
        }
        // // server return faultdata if fault is there and fault:none of fault is not there
        else if (serverData && !serverData.error) {
            setPronyData([serverData.freq, serverData.damping, serverData.amplitude])

        }
        else {

            setErr_message("Server error while detecting");
        }
    }
    // --------------- useeffect -----------------------------------------------------//


    useEffect(() => {
        resetPlotData();
    }, [property])
    useEffect(() => {
        // console.log('mode->0')
        setMode(0);
        setButtonText('Detect Event');
    }, [property, selectedLine, selectedSub])

    if (!data) {
        return (<Alert severity="error">No Data found to analyze, Please import a file</Alert>);
    }
    // if (selectedSub && selectedLine && property) { console.log(selectedSub, selectedLine, property) }
    // ---------------------------------------------------------//

    return (
        <div style={styles.container}>
            <div style={styles.flexContainer}>
                {!data && <Alert severity="error">No Data found to analyze !!</Alert>}
                {err_message && <AlertDialog props={[err_message, setErr_message]} />}
                <Grid style={styles.flexItem}>
                    <Grid >
                        <div style={styles.containerChild} >
                            <Typography style={styles.label} variant="h6">Substations</Typography>
                            <FormGroup>
                                {Object.keys(subLnData).map((subKey) => (
                                    <FormControlLabel
                                        key={subKey}
                                        control={
                                            <Checkbox
                                                checked={selectedSub === subKey}
                                                onChange={() => { setSelectedSub(subKey); setSelectedLine(null) }}
                                                size="caption"
                                            />
                                        }
                                        label={subKey}
                                    />
                                ))}
                            </FormGroup>
                        </div>
                        <Grid item style={styles.containerChild}>
                            {selectedSub && (
                                <div>
                                    <Typography style={styles.label} variant="h6">{selectedSub}:Lines</Typography>
                                    <FormGroup>
                                        {subLnData[selectedSub].map((line) => (
                                            <FormControlLabel
                                                key={line}
                                                control=
                                                {<Checkbox size="caption" checked={selectedLine === line}
                                                    onChange={() => setSelectedLine(line)} />}
                                                label={line}

                                            />
                                        ))}
                                    </FormGroup>
                                </div>
                            )}
                        </Grid>
                    </Grid>
                    <Button variant="contained" onClick={changeDataSetToPlot}>Add</Button>

                </Grid>
                <Grid style={styles.flexItem}>
                    <Grid>
                        {/* props={[time ? time : [], plotData, data, property]} */}
                        {<PlotlyPlotProny props={[time ? time : [], plotData ? plotData : [], 'Time', 'Frequency', "Selected data: " + `${selectedSub}` + ':' + `${selectedLine}`, setSelectedPoints]} />}
                    </Grid>
                    <Grid container margin={2} direction={'column'} justifyContent={'center'} alignItems={'center'}>
                        {mode === 0 && selectedSub && selectedLine && property === 'F' && plotData[0] && plotData[0].length > 0 && <Button variant="contained" onClick={handleDetectFaultButton}>{buttonText}</Button>}
                        {mode === 1 && selectedSub && selectedLine && property === 'F' && selectedPoints[0] && (
                            <Button variant="contained" sx={{ margin: 2 }} onClick={sendToServer}>Prony Analysis</Button>
                        )}
                    </Grid>


                </Grid>
                <Grid style={{ ...styles.flexItem, ...{ 'maxWidth': '30vw' } }}>
                    <Typography style={styles.label} variant="h6">Selected Points</Typography>
                    <p>Frequency: {selectedPoints[1]}</p>
                    <p>Time: {selectedPoints[0]}</p>
                    <Typography style={styles.label} variant="h6">Prony Data</Typography>
                    {pronyData && <table style={tableStyle}>
                        <thead>
                            <tr>
                                <th style={thStyle}>Frequency Components</th>
                                <th style={thStyle}>Damping ratios</th>
                                <th style={thStyle}>Amplitude</th>
                            </tr>
                        </thead>
                        <tbody>
                            {tableRows}
                        </tbody>
                    </table>}
                </Grid>

            </div>
        </div>

    );
}
export default PronyAnalysis;